import './index.css';
import React, {Component} from 'react';
import Office from './Office';
import UserGroup from './UserGroup';
import Status from './Status';
import Payment from './Payment';
import Color from './Color';
import Localization from './Localization';

import { connect } from "react-redux";
import { path } from "../../../store/actions/index";

const mapStateToProps = state => {
    return { path: state.path };
};

const mapDispatchToProps = dispatch => {
    return {
        changePath: paths => dispatch(path(paths))
    };
}


class Setting extends Component{
    constructor(props){
        super(props);
        this.state = {}
    }


    render(){
        return (
            <div style={{marginTop: "30px", marginLeft: "40px"}}>
                <div className="block-header"> <span className="main-setting"> </span><span className="user-setting"> </span> </div>
                  
                    <div className="wrap-container">
                        <div className="block-nav">
                            <ul className="block-contacts">
                                <li className="list-contacts"></li>
                                <li className="selected list-contacts" id="otdel-link" onClick={() => this.props.changePath('office')}></li>
                                <li className="selected list-contacts" id="group-client-link" onClick={() => this.props.changePath('group')}> </li>
                            </ul>
                            <ul className="block-order">
                                <li className="list-order"></li>
                                <li className="selected list-order" id="status-order-link" onClick={() => this.props.changePath('status')}> </li>
                                <li className="selected list-order" id="block-payment-method" onClick={() => this.props.changePath('payment')}> </li>
                                <li className="selected list-order"> </li>
                                <li className="selected list-order"></li>
                            </ul>
                            <ul className="block-catalog">
                                <li className="list-catalog"></li>
                                <li className="list-catalog hover-list"> </li>
                                <li className="list-catalog hover-list"></li>
                                <li className="list-catalog hover-list"></li>
                                <li className="list-catalog hover-list"></li>
                                <li className="list-catalog hover-list"></li>
                                <li className="list-catalog hover-list"> </li>
                                <li className="list-catalog hover-list"></li>
                                <li className="list-catalog hover-list" onClick={() => this.props.changePath('color')}> </li>
                                <li className="list-catalog hover-list" onClick={() => this.props.changePath('localization')}></li>
                            </ul>
                            <ul className="block-warehouse">
                                <li className="list-warehouse"></li>
                                <li className="list-warehouse hover-list"></li>
                            </ul>
                        </div>

                      {this.props.path === "office" && <Office />}
                      {this.props.path === "group" && <UserGroup />}
                      {this.props.path === "status" && <Status />}
                      {this.props.path === "payment" && <Payment />}
                      {this.props.path === "color" && <Color />}
                      {this.props.path === "localization" && <Localization />}

                    </div>
            </div>
        )
    }

}


export default connect(mapStateToProps, mapDispatchToProps)(Setting);

